import React, { Component } from 'react';
import { InputNumber } from 'antd';

export default class InputMoney extends Component {
  render() {
    return (
      <InputNumber
        formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
        parser={value => value.replace(/\$\s?|(,*)/g, '')}
        {...this.props}
      />
    );
  }
}



