import React, { Component } from 'react';
import { Form, Select, Col } from 'antd';

const { Option } = Select;
// eslint-disable-next-line react/prefer-stateless-function
export default class CourseInput extends Component {
  render() {
    const { getFieldDecorator } = this.props.form;
    const { studyFields, onChange } = this.props;
    return (
      <Col span={8}>
        <Form.Item label="CN">
          {getFieldDecorator('studyField', {})(
            <Select onChange={onChange}>
              {studyFields.map(item => (
                <Option key={item.id} value={item.id}>
                  {item.label}
                </Option>
              ))}
            </Select>
          )}
        </Form.Item>
      </Col>
    );
  }
}



